ed in many areas which it is needed.")]),_vm._v(" "),_c('li',[_c('strong',[_vm._v("Excess clothing/supplies (conversion to a market) (Tom's):")]),_vm._v(" People tend to send clothes during humanitarian crisis, and despite their good intentions, the reality is that most victims need medicine or hygiene products, over a new set of clothes. The wave of clothes that are donated is commonly dubbed \"the second disaster\". One problem with Tom's buy one send one policy, was the fact that it hurt small businesses by pushing them out of the market with free and better quality products.")]),_vm._v(" "),_c('li',[_c('strong',[_vm._v("Agriculture/loss of land (Easier to set up vertical farming):")]),_vm._v(" The loss of land/agriculture is a consistent issue for farmers during crisis. Vertical farming has been used as a method during humanitarian crisis to grow crops as it is more effective during times where water is not easily accessible. However, current methods aren't as cost efficient or effective, and do not allow production anywhere near where they were prior to a disaster.")]),_vm._v(" "),_c('li',[_c('strong',[_vm._v("Drones payload (Tons of current research):")]),_vm._v("Despite the convenience of drones, payload has been a big issue and the topic of much research since the rise of drones. The issue stems that while there are drones such as octopods, which can deliver heavier loads, they are extremely costly. Companies such as Zipline have moved away from the conventional drone and into robotic planes, which is used to carry 1kg of blood to places in need.")]),_vm._v(" "),_c('li',[_c('strong',[_vm._v("Tons of garbage (What else can you do?)")]),_vm._v(" The garbage issue exist as a bigger issue than simply as a humanitarian crisis. While tackling the entire garbage problem is out of our scope, we must considered ways waste is generated, specifically during a humanitarian crisis, and whether or not we have the capacity to reduce, reuse, and convert the waste into something useful.")]),_vm._v(" "),_c('li',[_c('strong',[_vm._v("What can Robotics do?:")]),_vm._v(" While all these problems undeniably have a solution, an important aspect of this is how we can apply robotics to it, and while this does come with limitations, it also opens the scope for greater possibilities.")])]),_c('br'),_vm._v("\n\t\t\t\t\t\t\tFiguring out the problem is the first step in this project, and is usually just as difficult as finding a solution. Members were asked to do research prior to coming to the meetings and give a short presentation on their findings. We discussed the existing solutions to such problems and possible ways to improve upon them. What we found to be the most difficult part was implementation of robotics, as some of these issues had solutions that were not robotics related. However, the team was still able to successfully identify possible solutions to these problems, incorporating robotics.\n\t\t\t\t\t\t")]),_vm._v(" "),_c('h2',[_vm._v("Discussed Robotic Solutions")]),_vm._v(" "),_c('p',[_c('ul',[_c('li',[_c('strong',[_vm._v("Sensor that detects fertile soil (farming):")]),_vm._v(" Loss of agriculture and destruction of land makes farming very difficult for people who rely on it as a source of income and food. A device that helps people detect fertile soil, would allow those in need to have a easier and more successful time farming. ")]),_vm._v(" "),_c('li',[_c('strong',[_vm._v("Detecting harmful chemicals on the road (trash):")]),_vm._v(" A big issue during humanitarian crisis is transportation, whether that be supplies or people. However, it is not always clear whether or not debris on the road is safe to be removed in the presence of humans. Such a sensor would allow humanitarian workers and others to traverse roads with peace of mind, and allow for the quicker clearing of roads.")]),_vm._v(" "),_c('li',[_c('strong',[_vm._v("Sensor that detects live people in rubble/Identifying people:")]),_vm._v(" Identifying people trapped below buildings has always been an issue. A bigger issue is when time is spent finding those who are already deceased. As unfortunate as it is, a humanitarian worker's time is precious and should be spent on trying to help those still alive. Although NASA has created a device that satisfies some of these needs, it still lacks mobility as well as wide range usage.")]),_vm._v(" "),_c('li',[_c('strong',[_vm._v("Convert excess clothes to something useful (Economic stability):")]),_vm._v(" Many small businesses are completely devastated after natural disasters or wartime conflicts. Such a tragedy not only strip a family of their income, but a market of its supplies. Because of that mass amounts of excess clothing that is shipped to those in need, so much so that most of it is simply burned or tossed aside, it holds opportunity to be recycled. Not only that if disaster victims could use a device that converts the clothing to something useful that was also marketable, it could also help them financially.")]),_vm._v(" "),_c('li',[_c('strong',[_vm._v(":")])])]),_c('br'),_vm._v("\n\t\t\t\t\t\t\tSimplifying the different humanitarian issues allowed us to come up with more specific and applicable problems that could potentially scaled upward. Each member was given two topics to do continued research on. Next week we hope to be better versed on the discussed solutions which will allow us to make a better decision moving forward in deciding what kind of project we want to pursue.\n\t\t\t\t\t\t")])])])],1)])])}
